import { Button, Icon } from '@wordpress/components';
import { useState } from '@wordpress/element';
import { chevronUp, chevronDown } from '@wordpress/icons';

const PanelSection = ( { title, children, initialOpen = false } ) => {
	const [ { isOpened }, setState ] = useState( {
		isOpened: initialOpen,
	} );
	return <div className={ `gutenify-components-panel__body components-panel__body ${ isOpened && 'is-opened' }` }>
		<h2
			className="components-panel__body-title"
		>
			<Button className="components-button components-panel__body-toggle"
				onClick={ () => {
					setState( {
						isOpened: ! isOpened,
					} );
				} }>
				{ title }
				<span aria-hidden="true">
					<Icon
						className="components-panel__arrow"
						icon={ isOpened ? chevronUp : chevronDown }
					/>
				</span>
			</Button>
		</h2>
		{ isOpened && <>{ children }</> }
	</div>;
};
export default PanelSection;
